<template>
  <div class="rainfall">
    <!-- 標題 -->
    <div class="row" style="margin: 0 15px 0 15px">
      <div class="col-12 text-center">
        <span
          class="badge badge-pill badge-info animate__animated animate__fadeInUp text-center title"
          style="animation-duration: 1s; animation-iteration-count: 1"
          >高雄地區降雨</span
        >
      </div>
    </div>
    <el-row>
      <el-col :span="10">
        <!-- 地圖 -->
        <l-map
          style="height: 500px; width: 100%; margin: 0 auto"
          :zoom="zoom"
          :options="options"
          :center="center"
          v-if="originalData.length !== 0"
        >
          <!-- 載入地圖資料 -->
          <l-tile-layer :url="url" :attribution="attribution" />
          <!-- 地標 -->
          <l-marker
            ref="location"
            :lat-lng="item.local"
            v-for="item in originalData"
            :key="item.name"
            @click="tableHighlight(item.SiteName)"
          >
            <l-icon :icon-url="iconimg" 
                    :shadow-url="shadowUrl"
                    :icon-size="iconSize"
                    :icon-anchor="iconAnchor"
                    :popup-anchor="popupAnchor"
                    :shadow-size="shadowSize"  />
            <!-- 地標圖示 -->
            <!-- <l-tooltip :options="tooltipOptions">
          <div class="tooltip-content">
            <div>{{ item.name }}</div>
          </div>
            </l-tooltip>-->
            <!-- 點擊圖標會彈出顯示的文字 -->
            <l-popup style="text-align: center">
              <div>{{ item.SiteName }}</div>
            </l-popup>
          </l-marker>
        </l-map>
      </el-col>
      <!-- 表格 -->
      <el-col :span="14">
        <el-table
          v-if="rainfallData.length !== 0"
          :data="rainfallData"
          height="500"
          border
          style="width: 100%"
        >
          <el-table-column
            sortable
            prop="Township"
            label="地區"
            width="180"
          ></el-table-column>
          <el-table-column
            sortable
            prop="SiteName"
            label="測站名稱"
          ></el-table-column>
          <el-table-column
            sortable
            prop="Rainfall10min"
            label="10分鐘累積雨量"
          ></el-table-column>
          <el-table-column
            sortable
            prop="Rainfall1hr"
            label="1小時累積雨量"
          ></el-table-column>
          <el-table-column
            sortable
            prop="Rainfall3hr"
            label="3小時累積雨量"
          ></el-table-column>
          <el-table-column
            sortable
            prop="Rainfall6hr"
            label="6小時累積雨量"
          ></el-table-column>
          <el-table-column
            sortable
            prop="Rainfall12hr"
            label="12小時累積雨量"
          ></el-table-column>
          <el-table-column
            sortable
            prop="Rainfall24hr"
            label="24小時累積雨量"
          ></el-table-column>
        </el-table>
      </el-col>
    </el-row>
  </div>
</template>
<script>
import axios from "axios";

export default {
  mounted() {
    this.getMap();
  },
  data() {
    return {
      // ---------------------地圖資料---------------------
      //地圖縮放比例
      zoom: 9,
      //地圖中心
      center: [23.013986, 120.586665],
      // 地標圖示
      iconimg: require("@/assets/img/rainfallicon.png"),
      iconSize: [24, 30],
      iconAnchor: [12, 41], // size of the shadow
      popupAnchor: [1, -34], // point of the icon which will correspond to marker's location
      shadowSize: [10, 10], // point from which the popup should open relative to the iconAnchor
                        
      //地圖使用網址
      url: "https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png",
      //地圖來源聲明    
      attribution: `© <a href="http://osm.org/copyright">OpenStreetMap</a> contributors`,
      // 取消顯示地圖左上角縮放地圖的按鈕
      options: {
        zoomControl: false,
      },      
      // ---------------------地圖打點資料---------------------
      rainfallData: [],
      originalData: [],      
    };
  },
  methods: {
    async getMap() {
      console.log("wait");
      // 取得降雨資料
      await axios
        .get(
          "https://opendata.epa.gov.tw/api/v1/RainTenMin?%24skip=0&%24top=1000&%24format=json"
        )
        .then((res) => {
          console.log(res);
          let kaosiungData = res.data.filter((obj) => {
            return obj.County === "高雄市";
          });
          console.log(kaosiungData);
          kaosiungData.forEach((item) => {
            let obj = {
              SiteName: item.SiteName,
              Township: item.Township,
              local: [item.TWD67Lat, item.TWD67Lon],
              Rainfall10min: item.Rainfall10min,
              Rainfall1hr: item.Rainfall1hr,
              Rainfall3hr: item.Rainfall3hr,
              Rainfall6hr: item.Rainfall6hr,
              Rainfall12hr: item.Rainfall12hr,
              Rainfall24hr: item.Rainfall24hr,
            };
            this.originalData.push(obj);
          });
          this.rainfallData = this.originalData;
          console.log(this.originalData);
          console.log(this.rainfallData);
        })
        .catch((err) => {
          console.log(err);
        });
    },
    // 點選地圖上的地標
    tableHighlight(val) {
      console.log(val);
      // 將資料的第一筆更改為點擊的那個位置的資料
      let data = this.originalData.filter((item) => {
        return item.SiteName === val;
      });
      this.rainfallData = this.originalData.filter((item) => {
        return item.SiteName !== val;
      });
      this.rainfallData.splice(0, 0, ...data);
      console.log(this.rainfallData);
      // 更改表格第一列的資料底色
      let highlightRow = document.getElementsByClassName("el-table__row")[0];
      console.log(highlightRow);
      highlightRow.style.backgroundColor = "#AAFFEE";
    },
  },
};
</script>
<style scoped lang="scss">
.rainfall {
  padding: 2rem 2rem;
  background-color: #e3f0f7;
  font-family: 微軟正黑體;
  .title {
    margin: 0 auto 3% auto;
    border-color: #dcdcdc;
    border-width: 2px;
    padding: 0.5% 0 0.5% 1%;
    line-height: 60px;
    letter-spacing: 20px;
    text-decoration: none;
    font-weight: 600;
    outline: 0;
    font-size: 2.5em;
  }
}

</style>